.backButton {
  left: 2%; /* Adjust left position relative to the viewport width */
  bottom: 5%; /* Adjust bottom position relative to the viewport height */
  background-color: #222;
  border-radius: 4px;
  border-style: none;
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  font-family: "Farfetch Basis", "Helvetica Neue", Arial, sans-serif;
  font-size: 0.9vw; /* Adjust font size relative to the viewport width */
  font-weight: 700;
  line-height: 1.5;
  margin: 0;
  max-width: none;
  min-height: 3vh; /* Adjust minimum height relative to the viewport height */
  min-width: 10%; /* Adjust minimum width relative to the viewport width */
  outline: none;
  overflow: hidden;
  padding: 0.5vw 1vw; /* Adjust padding relative to the viewport width */
  position: absolute;
  text-align: center;
  text-transform: none;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  width: 2%; /* Adjust width relative to the viewport width */
}

.backButton:hover,
.backButton:focus {
  opacity: 0.75;
}

