.btn-msc {
  background-color: #01aa48;
  transition: all 0.3s;
  position: relative;
  position: absolute;
  bottom: 12.9vw;
  left: 15%;
  width: 27%;
  height: 5vw;
  text-align: center;
  padding-top: 1vw;
}
.btn-msc span {
  transition: all 0.3s;
}
.btn-msc::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  opacity: 0;
  transition: all 0.3s;
  border-top-width: 0.1vw;
  border-bottom-width: 0.1vw;
  border-top-style: solid;
  border-bottom-style: solid;
  border-top-color: rgba(255, 255, 255, 0.5);
  border-bottom-color: rgba(255, 255, 255, 0.5);
  transform: scale(0.1, 1);
}
.btn-msc:hover span {
  letter-spacing: 0.1vw;
}
.btn-msc:hover::before {
  opacity: 1;
  transform: scale(1, 1);
}
.btn-msc::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  transition: all 0.3s;
  background-color: rgba(255, 255, 255, 0.1);
}
.btn-msc:hover::after {
  opacity: 0;
  transform: scale(0.1, 1);
}

